@charset "UTF-8";
/* GLOBAL */
html {
  height: 100%;
}

body {
  min-height: 100%;
  margin: 0 auto;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  color: #333333;
}

img {
  max-width: 100%;
  object-fit: contain;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

p,
h1,
h2,
h3,
h4 {
  margin: 0;
  padding: 0;
}

/* здесь подключите шрифты*/
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url("./../fonts/montserrat-400.woff2") format("woff2"), url("./../fonts/montserrat-400.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  src: url("./../fonts/montserrat-500.woff2") format("woff2"), url("./../fonts/montserrat-500.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: url("./../fonts/montserrat-600.woff2") format("woff2"), url("./../fonts/montserrat-600.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  src: url("./../fonts/montserrat-700.woff2") format("woff2"), url("./../fonts/montserrat-700.woff") format("woff");
  font-display: swap;
}
/* BLOCKS */
.header {
  min-height: 60px;
}

.header__wrapper {
  width: 100%;
  position: relative;
  top: 0;
  z-index: 10;
  display: grid;
  grid-template-columns: auto 1fr auto;
  grid-template-areas: "logo user toggle" "site site site";
  box-sizing: border-box;
}
.header__wrapper--nojs {
  position: relative;
}
@media (min-width: 768px) {
  .header__wrapper {
    grid-template-areas: "logo site user";
    height: 80px;
    padding: 0 20px;
  }
}
@media (min-width: 1150px) {
  .header__wrapper {
    max-width: 1350px;
    grid-template-columns: auto auto 1fr auto;
    grid-template-areas: "logo description site user";
    margin: 0 auto;
  }
}

.header__logo {
  grid-area: logo;
  display: grid;
  place-items: center;
  padding: 0 20px;
  outline: none;
}
@media (min-width: 768px) {
  .header__logo {
    padding: 0;
  }
}

.header__description {
  display: none;
}
@media (min-width: 1150px) {
  .header__description {
    display: flex;
    align-items: center;
    margin-left: 20px;
    grid-area: description;
    font-size: 12px;
    line-height: 16px;
    width: 130px;
  }
}

.header__user-nav {
  grid-area: user;
}

.header__site-nav {
  grid-area: site;
  position: absolute;
  width: 100%;
  background-color: #ffffff;
  z-index: 2;
}
@media (max-width: 767px) {
  .header__site-nav--closed {
    display: none;
  }
  .header__site-nav--open::after {
    position: absolute;
    content: "";
    top: 0;
    height: 1px;
    background-color: #f0ebff;
    left: 0;
    right: 0;
  }
}
@media (width: 767px) {
  .header__site-nav--closed {
    display: none;
  }
}
.header__site-nav--nojs {
  position: relative;
  display: block;
}
@media (min-width: 768px) {
  .header__site-nav {
    height: 100%;
  }
}

.header__toggle {
  cursor: pointer;
  grid-area: toggle;
  position: absolute;
  right: 0;
  border: none;
  width: 60px;
  height: 60px;
}
.header__toggle--open {
  background: url("./../img/header-menu-open.svg") #ffffff no-repeat 50% 50%;
}
.header__toggle--closed {
  background: url("./../img/header-menu-closed.svg") #ffffff no-repeat 50% 50%;
}
.header__toggle--nojs {
  display: none;
}
.header__toggle:active {
  background-color: #f0ebff;
}
@media (min-width: 768px) {
  .header__toggle {
    display: none;
  }
}

.header__picture {
  display: flex;
  align-items: center;
}

@media (min-width: 768px) {
  .header__image {
    display: block;
    width: 82px;
    height: auto;
  }
}
@media (min-width: 1150px) {
  .header__image {
    width: 170px;
  }
}

.user__wrapper {
  display: flex;
  justify-content: flex-end;
}

.user-list__button {
  width: 60px;
  height: 60px;
  display: grid;
  place-items: center;
  border-radius: 0;
  background-color: #ffffff;
  border: none;
  outline: none;
}
.user-list__button:active {
  background-color: #f0ebff;
}
@media (min-width: 768px) {
  .user-list__button {
    height: 100%;
    display: flex;
    gap: 8px;
    padding: 0 20px;
  }
  .user-list__button:hover .user-list__text, .user-list__button:focus .user-list__text {
    color: #7859cf;
  }
  .user-list__button:hover .page-header__cart-icon, .user-list__button:hover .page-header__user-icon {
    fill: #7859cf;
  }
  .user-list__button:focus .page-header__cart-icon, .user-list__button:focus .page-header__user-icon {
    fill: #7859cf;
  }
}
@media (min-width: 1150px) {
  .user-list__button {
    width: auto;
  }
}

.user-list__text {
  display: none;
}
@media (min-width: 1150px) {
  .user-list__text {
    display: block;
    font-weight: 500;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    color: #333333;
  }
}

.page-header__cart-icon,
.page-header__user-icon {
  fill: #333333;
}

.site-list {
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .site-list {
    flex-direction: row;
    justify-content: center;
    height: 100%;
  }
}
@media (min-width: 1150px) {
  .site-list {
    gap: 20px;
  }
}

.site-list__item {
  position: relative;
  display: grid;
  place-items: center;
  height: 60px;
  border-bottom: 1px solid #f0ebff;
  box-sizing: border-box;
}
.site-list__item:active {
  background-color: #f0ebff;
}
@media (min-width: 768px) {
  .site-list__item {
    border-bottom: none;
    height: auto;
    align-items: center;
  }
}

.site-list__link {
  font-size: 12px;
  line-height: 16px;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  text-decoration: none;
  text-transform: uppercase;
  color: #333333;
  cursor: pointer;
  border-bottom: 2px solid transparent;
}
@media (min-width: 768px) {
  .site-list__link {
    padding: 0 20px;
    box-sizing: border-box;
    position: relative;
  }
  .site-list__link--current {
    border-bottom: 2px solid #7859cf;
    color: #7859cf;
  }
  .site-list__link:hover, .site-list__link:focus-within {
    color: #7859cf;
    outline: none;
  }
  .site-list__link:active {
    background-color: #f0ebff;
  }
}

@media (min-width: 1150px) {
  div.swiper-slide {
    margin: 0 auto;
  }
}

.slide__container {
  display: flex;
  align-items: center;
  flex-direction: column;
  box-sizing: border-box;
  min-height: 640px;
  padding: 20px 20px 0 20px;
}
@media (min-width: 768px) {
  .slide__container {
    display: grid;
    align-items: center;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(5, fit-content(200px));
    grid-template-areas: "mark" "title" "text" "buy-block" "image";
    min-height: 876px;
    padding: 60px 80px 0 80px;
  }
}
@media (min-width: 1150px) {
  .slide__container {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: repeat(7, fit-content(200px));
    grid-template-areas: "image ." "image mark" "image title" "image text" "image buy-block" "image .";
    min-height: 714px;
    padding: 30px 0 0 0;
    column-gap: 30px;
  }
}

.slide__flat-white {
  background-color: #f3ebe1;
}
@media (min-width: 768px) {
  .slide__flat-white {
    background: linear-gradient(180deg, #f3ebe1 75%, #ffffff 75%);
  }
}
@media (min-width: 1150px) {
  .slide__flat-white {
    background: #f3ebe1;
  }
}

.slide__espresso {
  background-color: #e5e6e8;
}
@media (min-width: 768px) {
  .slide__espresso {
    background: linear-gradient(180deg, #e5e6e8 75%, #ffffff 75%);
  }
}
@media (min-width: 1150px) {
  .slide__espresso {
    background: #e5e6e8;
  }
}

.slide__latte {
  background-color: #eae6fc;
}
@media (min-width: 768px) {
  .slide__latte {
    background: linear-gradient(180deg, #eae6fc 75%, #ffffff 75%);
  }
}
@media (min-width: 1150px) {
  .slide__latte {
    background: #eae6fc;
  }
}

.slide__image {
  margin-bottom: 10px;
  background-color: transparent;
}
@media (min-width: 768px) {
  .slide__image {
    display: flex;
    align-items: center;
    grid-area: image;
    justify-content: center;
  }
}
@media (min-width: 1150px) {
  .slide__image {
    align-items: flex-end;
    justify-content: flex-end;
  }
}

@media (min-width: 768px) {
  .slide__image img {
    width: 476px;
    height: 476px;
  }
}
@media (min-width: 1150px) {
  .slide__image img {
    width: 600px;
    height: 600px;
  }
}

.slide__new-mark {
  margin-bottom: 6px;
  font-size: 12px;
  font-weight: 700;
  line-height: 16px;
  color: #7859cf;
  letter-spacing: 0.04em;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .slide__new-mark {
    grid-area: mark;
    text-align: center;
  }
}
@media (min-width: 1150px) {
  .slide__new-mark {
    max-width: 600px;
    text-align: start;
  }
}

.slide__title {
  margin-bottom: 10px;
  padding: 0 20px;
  font-size: 36px;
  font-weight: 700;
  line-height: 48px;
  text-align: center;
  letter-spacing: 0.02em;
}
@media (min-width: 768px) {
  .slide__title {
    grid-area: title;
    margin-bottom: 16px;
    font-size: 56px;
    line-height: 80px;
  }
}
@media (min-width: 1150px) {
  .slide__title {
    max-width: 600px;
    padding: 0;
    font-size: 56px;
    line-height: 80px;
    text-align: start;
  }
}

.slide__text {
  margin-bottom: 20px;
  padding: 0 10px;
  font-size: 14px;
  line-height: 20px;
  text-align: center;
}
@media (min-width: 768px) {
  .slide__text {
    grid-area: text;
    font-size: 20px;
    line-height: 32px;
  }
}
@media (min-width: 1150px) {
  .slide__text {
    max-width: 600px;
    margin-bottom: 40px;
    padding: 0;
    text-align: start;
  }
}

.slide__buy-block {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 25px;
}
@media (min-width: 768px) {
  .slide__buy-block {
    grid-area: buy-block;
    justify-content: center;
    margin-bottom: 20px;
    padding: 0;
  }
}
@media (min-width: 1150px) {
  .slide__buy-block {
    justify-content: flex-start;
    max-width: 600px;
  }
}

.slide__old-price {
  margin-right: 6px;
  line-height: 32px;
  color: #7859cf;
  text-decoration: line-through;
}

.slide__new-price {
  margin-right: 20px;
  font-size: 20px;
  font-weight: 700;
  line-height: 32px;
}

div .swiper-button-next,
div .swiper-button-prev {
  top: 0;
  bottom: 0;
  width: 40px;
  height: 100%;
  margin: 0;
}
@media (min-width: 768px) {
  div .swiper-button-next,
div .swiper-button-prev {
    width: 80px;
  }
}
div .swiper-button-next::after,
div .swiper-button-prev::after {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 11px;
  height: 20px;
  content: "";
  transform: translate(-50%, -50%);
}

div .swiper-button-prev {
  left: 0;
}
div .swiper-button-prev::after {
  background-image: url("./../img/arrow-left.svg");
}
div .swiper-button-prev:hover, div .swiper-button-prev:focus {
  outline: none;
  background: linear-gradient(to right, #ffffff 0%, rgba(255, 255, 255, 0) 100%);
}
div .swiper-button-prev:active {
  background: linear-gradient(to right, rgba(120, 89, 207, 0.25) 0%, rgba(120, 89, 207, 0) 100%);
}

div .swiper-button-next {
  right: 0;
}
div .swiper-button-next::after {
  background-image: url("./../img/arrow-right.svg");
}
div .swiper-button-next:hover, div .swiper-button-next:focus {
  outline: none;
  background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, #ffffff 100%);
}
div .swiper-button-next:active {
  background: linear-gradient(to right, rgba(120, 89, 207, 0) 0%, rgba(120, 89, 207, 0.25) 100%);
}

div .swiper-pagination-bullet {
  display: none;
}
@media (min-width: 1150px) {
  div .swiper-pagination-bullet {
    display: inline-block;
    width: 62px;
    height: 4px;
    opacity: 1;
    border-radius: 0;
    background-color: #ffffff;
  }
  div .swiper-pagination-bullet:hover, div .swiper-pagination-bullet:focus {
    background-color: rgba(120, 89, 207, 0.25);
  }
}

@media (min-width: 1150px) {
  div .swiper-pagination-bullet-active {
    background-color: #7859cf;
  }
  div .swiper-pagination-bullet-active:hover, div .swiper-pagination-bullet-active:focus {
    background-color: #7859cf;
  }
}

div .swiper-horizontal > .swiper-pagination-bullets,
div .swiper-pagination-bullets.swiper-pagination-horizontal,
div .swiper-pagination-custom,
.swiper-pagination-fraction {
  bottom: 70px;
}

@font-face {
  font-family: "swiper-icons";
  src: url("data:application/font-woff;charset=utf-8;base64, ");
  font-weight: 400;
  font-style: normal;
}
:root {
  --swiper-theme-color: #007aff;
}

.swiper {
  position: relative;
  z-index: 1;
  overflow: hidden;
  margin-right: auto;
  margin-left: auto;
  padding: 0;
  list-style: none;
}

.swiper-vertical > .swiper-wrapper {
  flex-direction: column;
}

.swiper-wrapper {
  position: relative;
  z-index: 1;
  display: flex;
  box-sizing: content-box;
  width: 100%;
  height: 100%;
  transition-property: transform;
}

.swiper-android .swiper-slide,
.swiper-wrapper {
  transform: translate3d(0, 0, 0);
}

.swiper-pointer-events {
  touch-action: pan-y;
}

.swiper-pointer-events.swiper-vertical {
  touch-action: pan-x;
}

.swiper-slide {
  position: relative;
  flex-shrink: 0;
  width: 100%;
  height: 100%;
  transition-property: transform;
}

.swiper-slide-invisible-blank {
  visibility: hidden;
}

.swiper-autoheight,
.swiper-autoheight .swiper-slide {
  height: auto;
}

.swiper-autoheight .swiper-wrapper {
  align-items: flex-start;
  transition-property: transform, height;
}

.swiper-backface-hidden .swiper-slide {
  transform: translateZ(0);
  backface-visibility: hidden;
}

.swiper-3d,
.swiper-3d.swiper-css-mode .swiper-wrapper {
  perspective: 1200px;
}

.swiper-3d .swiper-cube-shadow,
.swiper-3d .swiper-slide,
.swiper-3d .swiper-slide-shadow,
.swiper-3d .swiper-slide-shadow-bottom,
.swiper-3d .swiper-slide-shadow-left,
.swiper-3d .swiper-slide-shadow-right,
.swiper-3d .swiper-slide-shadow-top,
.swiper-3d .swiper-wrapper {
  transform-style: preserve-3d;
}

.swiper-3d .swiper-slide-shadow,
.swiper-3d .swiper-slide-shadow-bottom,
.swiper-3d .swiper-slide-shadow-left,
.swiper-3d .swiper-slide-shadow-right,
.swiper-3d .swiper-slide-shadow-top {
  position: absolute;
  z-index: 10;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.swiper-3d .swiper-slide-shadow {
  background: rgba(0, 0, 0, 0.15);
}

.swiper-3d .swiper-slide-shadow-left {
  background-image: linear-gradient(to left, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

.swiper-3d .swiper-slide-shadow-right {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

.swiper-3d .swiper-slide-shadow-top {
  background-image: linear-gradient(to top, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

.swiper-3d .swiper-slide-shadow-bottom {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

.swiper-css-mode > .swiper-wrapper {
  overflow: auto;
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.swiper-css-mode > .swiper-wrapper::-webkit-scrollbar {
  display: none;
}

.swiper-css-mode > .swiper-wrapper > .swiper-slide {
  scroll-snap-align: start start;
}

.swiper-horizontal.swiper-css-mode > .swiper-wrapper {
  scroll-snap-type: x mandatory;
}

.swiper-vertical.swiper-css-mode > .swiper-wrapper {
  scroll-snap-type: y mandatory;
}

.swiper-centered > .swiper-wrapper::before {
  flex-shrink: 0;
  order: 9999;
  content: "";
}

.swiper-centered.swiper-horizontal > .swiper-wrapper > .swiper-slide:first-child {
  margin-inline-start: var(--swiper-centered-offset-before);
}

.swiper-centered.swiper-horizontal > .swiper-wrapper::before {
  width: var(--swiper-centered-offset-after);
  height: 100%;
  min-height: 1px;
}

.swiper-centered.swiper-vertical > .swiper-wrapper > .swiper-slide:first-child {
  margin-block-start: var(--swiper-centered-offset-before);
}

.swiper-centered.swiper-vertical > .swiper-wrapper::before {
  width: 100%;
  min-width: 1px;
  height: var(--swiper-centered-offset-after);
}

.swiper-centered > .swiper-wrapper > .swiper-slide {
  scroll-snap-align: center center;
}

.swiper-virtual .swiper-slide {
  transform: translateZ(0);
  backface-visibility: hidden;
}

.swiper-virtual.swiper-css-mode .swiper-wrapper::after {
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  pointer-events: none;
}

.swiper-virtual.swiper-css-mode.swiper-horizontal .swiper-wrapper::after {
  width: var(--swiper-virtual-size);
  height: 1px;
}

.swiper-virtual.swiper-css-mode.swiper-vertical .swiper-wrapper::after {
  width: 1px;
  height: var(--swiper-virtual-size);
}

.swiper-button-next,
.swiper-button-prev {
  position: absolute;
  z-index: 10;
  top: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  width: calc(var(--swiper-navigation-size) / 44 * 27);
  height: var(--swiper-navigation-size);
  margin-top: calc(0px - var(--swiper-navigation-size) / 2);
  color: var(--swiper-navigation-color, var(--swiper-theme-color));
  cursor: pointer;
}

.swiper-button-next.swiper-button-disabled,
.swiper-button-prev.swiper-button-disabled {
  opacity: 0.35;
  cursor: auto;
  pointer-events: none;
}

.swiper-button-next.swiper-button-hidden,
.swiper-button-prev.swiper-button-hidden {
  opacity: 0;
  cursor: auto;
  pointer-events: none;
}

.swiper-navigation-disabled .swiper-button-next,
.swiper-navigation-disabled .swiper-button-prev {
  display: none;
}

.swiper-button-next::after,
.swiper-button-prev::after {
  font-size: var(--swiper-navigation-size);
  font-variant: initial;
  line-height: 1;
  letter-spacing: 0;
  text-transform: none;
}

.swiper-button-prev,
.swiper-rtl .swiper-button-next {
  right: auto;
  left: 10px;
}

.swiper-button-prev::after,
.swiper-rtl .swiper-button-next::after {
  content: "prev";
}

.swiper-button-next,
.swiper-rtl .swiper-button-prev {
  right: 10px;
  left: auto;
}

.swiper-button-next::after,
.swiper-rtl .swiper-button-prev::after {
  content: "next";
}

.swiper-button-lock {
  display: none;
}

.swiper-pagination {
  position: absolute;
  z-index: 10;
  transition: 0.3s opacity;
  transform: translate3d(0, 0, 0);
  text-align: center;
}

.swiper-pagination.swiper-pagination-hidden {
  opacity: 0;
}

.swiper-pagination-disabled > .swiper-pagination,
.swiper-pagination.swiper-pagination-disabled {
  display: none;
}

.swiper-horizontal > .swiper-pagination-bullets,
.swiper-pagination-bullets.swiper-pagination-horizontal,
.swiper-pagination-custom,
.swiper-pagination-fraction {
  bottom: 10px;
  left: 0;
  width: 100%;
}

.swiper-pagination-bullets-dynamic {
  overflow: hidden;
