.control {
  display: flex;
  position: relative;
  align-self: center;
  min-height: 24px;
}

.control__mark {
  position: relative;
  box-sizing: border-box;
  border: 2px solid #bdbdbd;
}

.control__input[type=radio] + .control__mark {
  border-radius: 50%;
  width: 24px;
  height: 24px;
}
.control__input[type=radio] + .control__mark:hover, .control__input[type=radio] + .control__mark:focus {
  border: 2px solid #9070ec;
  outline: none;
}
.control__input[type=radio]:hover + .control__mark::after, .control__input[type=radio] + .control__mark:focus::after {
  position: absolute;
  z-index: 2;
  top: 50%;
  left: 50%;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: #7859cf;
  opacity: 0.25;
  content: "";
  transform: translate(-50%, -50%);
}
.control__input[type=radio]:checked + .control__mark {
  border: 2px solid #7859cf;
}
.control__input[type=radio]:checked + .control__mark::before {
  position: absolute;
  z-index: 3;
  top: 50%;
  left: 50%;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: #7859cf;
  content: "";
  transform: translate(-50%, -50%);
}
.control__input[type=checkbox] + .control__mark {
  border-radius: 12px;
  width: 42px;
  height: 24px;
  background-color: #bdbdbd;
}
.control__input[type=checkbox] + .control__mark:hover, .control__input[type=checkbox] + .control__mark:focus {
  border: 2px solid #7859cf;
  background-color: rgba(120, 89, 207, 0.25);
  outline: none;
}
.control__input[type=checkbox] + .control__mark::before {
  position: absolute;
  top: 50%;
  left: 0;
  border-radius: 50%;
  background-color: #ffffff;
  width: 20px;
  height: 20px;
  content: "";
  transform: translateY(-50%);
}
.control__input[type=checkbox]:checked + .control__mark {
  background-color: #7859cf;
  border: 2px solid #7859cf;
}
.control__input[type=checkbox]:checked + .control__mark::before {
  position: absolute;
  top: 50%;
  left: 18px;
  border-radius: 50%;
  background-color: #ffffff;
  width: 20px;
  height: 20px;
  content: "";
  transform: translateY(-50%);
}
.control__input:disabled + .control__mark, .control__input:disabled:hover + .control__mark, .control__input:disabled + .control__mark:focus {
  border-color: #e2e2e2;
}
.control__input[type=radio]:disabled + .control__mark::after {
  display: none;
}
.control__input[type=radio]:checked:disabled + .control__mark {
  border: 2px solid #e2e2e2;
}
.control__input[type=radio]:checked:disabled + .control__mark::before {
  background-color: #e2e2e2;
}
.control__input[type=checkbox]:disabled + .control__mark {
  background-color: #e2e2e2;
}
.control__input[type=checkbox]:checked:disabled + .control__mark::before {
  background-color: #bdbdbd;
}
.control__input[type=checkbox]:checked:disabled + .control__mark {
  border-color: #e2e2e2;
}
.control__input:disabled ~ .control__label {
  color: #bdbdbd;
}

